body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body{
background-color: whitesmoke;
font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif 
}

nav{
background:white;
height: 3rem;
width: 100%;   
display: flex;
justify-content: center;
align-items: center;
box-shadow: rgba(50, 50, 93, 0.293) 0px 2px 5px -1px, rgba(0, 0, 0, 0.304) 0px 1px 3px -1px;
}

.userReg{
font-weight: bold;
}

.container{
    max-width: 80rem;
    height: 90vh;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.spanAll{
    position: relative;
    height: 90%;
    max-width: 30rem;
    display: flex;
    justify-content: center;
    background: white;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 20px 20px ;
    margin-bottom: 30px;
}

.circleOverlay {
    position: absolute;
    width: 100%;
    height: 30%;
    background: rgb(115,225,231);
    background: linear-gradient(153deg, rgba(115,225,231,1) 28%, rgba(141,211,233,1) 73%);
    top: 0;
    right: 0;
    border-radius: 20px 20px 65% 30%;
    z-index: 0;
    transition: 1s ease-in-out;
}

.signUp{
    position: absolute;
    top: 20px;
    left: 40px;
    color: white;
}


.profileImage{
    display: flex;
    justify-content: center;
    margin-top: 2rem;
    margin-bottom: 0.5rem;
}

.spanAllContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 25rem;
    height: 22rem;
    margin-top: 90px;
}

.memeImg{
    height: 8rem;
    width: 8rem;
    border-radius: 50%;
}

.registerNameh2{
    height: 1rem;
    margin: 0 0 20px 0;
}

.contentEl{
    display: grid;
    justify-content: center;
    align-items: center;
    /* width: 100%; */
}

.eachBox{
display: flex;
margin: 0.3rem 0rem;
padding: 0.7rem;
border: 1px solid rgba(17, 153, 168, 0.366);
width: 13rem;
border-radius: 30px;
}

.eachBox:hover{
background-color: white;
padding: 0.7rem;
box-shadow: rgba(17, 153, 168, 0.366) 0px 1px 3px,  rgba(17, 153, 168, 0.366) 0px 1px 2px;
}

form{
    z-index: 0;
    margin-top:30px
}

input:-webkit-autofill {
    -webkit-box-shadow:0 0 0 50px white inset; /* Change the color to your own background color */
    -webkit-text-fill-color: #333;
}

input:-webkit-autofill:focus {
    -webkit-box-shadow: 0 0 0 50px white inset;
    -webkit-text-fill-color: #333;
}  

.formInputs{
    border: none;
    outline: none;
    font-size: 1rem;
    background-color: white;
}

.greenTick{
    color: #73e1e7;
    -webkit-transform: scale(2);
            transform: scale(2);
    transition: 0.5s ease-in;    
}

.blackTick{
    color: white;
}

.pwordError{
    color: red;
    font-weight: bold;
}

.formMarketing{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
}

#joinMembership{
  accent-color:rgb(17, 153, 168);
}

.submitBtn{
    position: absolute;
    margin: 1rem;
    font-size: 1rem;
    background: linear-gradient(153deg, rgba(115, 225, 231, 0.374) 28%, rgba(141, 212, 233, 0.374) 73%);
    color: white;
    padding: 0.5rem 1.5rem;
    border-radius: 50px 50px;
    border: none;
    cursor: pointer;
    bottom: 30px;
    transition: 0.3s ease-out;
}

.submitBtn:hover{
    margin: 1rem;
    font-size: 1rem;
    background: linear-gradient(153deg, rgba(115,225,231,1) 28%, rgba(141,211,233,1) 73%);
    color: white;
    border-radius: 50px 50px;
    border: none;
    cursor: pointer;
    transition: 0.3s ease-in; 
    -webkit-transform: scale(1.1); 
            transform: scale(1.1);
    padding: 0.5rem 5rem;
}

.Footer{
    height: 3rem;
    font-size: 0.8rem;
    width: 100%;
    background-color: white;
    display: flex;
    position: fixed;
    bottom: 0;
    justify-content: center;
    align-items: center;

    }

/*///////// Mobile Responsive /////*/


@media (max-width: 450px ){

    *{
        font-size: 0.8rem;
    }

    .container{
        margin: 1rem;
    }

    .spanAll{
        height: 90%;
    }

    .profileImage{
        margin-top: 0px;
    }

    form{
        margin-top: 0;
    }

    .signUp > h1{
        font-size: 2rem;
    }

    .signUp > p{
        font-size: 1rem;
    }

    .registerNameh2{
        font-size: 1.3rem;
    }
}


.modalContainer{
background-color: white;
opacity: 1;
box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
position: fixed;
font-size: 1.5rem;
min-height: 20rem;
min-width: 40rem;
border-radius: 15px;

}

.contentModalContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.closeBtn{
    display: flex;
    justify-content: flex-end;
    font-size: 1.2rem;
    outline: none;
    margin-right: 0.5rem;
}

.closeBtnSpan{
    cursor: pointer;
    opacity: 0.5;
}

.checkContainer{
    display: flex;
    justify-content: center;
    font-size: 4rem;
    color: #62E03A;
}

.downloadBtn{
    font-size: 1rem;
    background: linear-gradient(153deg, rgb(115, 225, 231) 28%, rgb(141, 212, 233) 73%);
    color: white;
    padding: 0.5rem 1.5rem;
    border-radius: 50px 50px;
    border: none;
    cursor: pointer;
    margin-bottom: 20px;
    text-decoration: none;
}

/*///////// Mobile Responsive /////*/

@media (max-width: 450px ){

    .modalContainer{
        min-width:0%;
        min-height: 0%;
        padding: 0.5rem;
    }

    .contentModalContainer{
    margin: 0 20px;
    }

    .BsPatchCheck{
        height: 2em;
        width: 2em;
    }

}

